import css from './Projects.module.css';
import { useSelector } from 'react-redux';
import fallbackImageUrl from '../../images/logo.jpg';

const SlideHumanitarianAid = () => {
  const language = useSelector(state => state.language);

  const title =
    language === 'de'
      ? 'Humanitre Hilfe'
      : language === 'en'
      ? 'Humanitarian aid'
      : ' ';
  const description =
    language === 'de'
      ? 'Die Gemeinntzige Vereine Ziele der Zukunft e.V bertrgt humanitre Hilfe an die Ukrainische Landsleute in Thringen e.V. fr die weitere Entsendung und den Transfer an bedrftige Brger in den betroffenen Regionen Ukraine.'
      : language === 'en'
      ? 'The non-profit organization "Ziele der Zukunft e.V" transfers humanitarian aid to the Ukrainische Landsleute in Thringen e.V. for further dispatch and transfer to needy citizens in the affected regions of Ukraine.'
      : '  "Ziele der Zukunft e.V."                  .';

  return (
    <div className={css.slideWrap}>
      <div className={css.slideImgWrap}>
        <img
          className={css.slideImg}
          src="https://res.cloudinary.com/dqmpdikvc/image/upload/v1710849960/Humanitarian_aid_5_accwqo.jpg"
          alt="Humanitre Hilfe"
          width="100%"
          onError={e => {
            e.target.onerror = null;
            e.target.src = fallbackImageUrl;
          }}
        />
        <div className={css.overlay}>
          <p className={css.slideDescription}>{description}</p>
        </div>
      </div>
      <div className={css.slideTextWrap}>
        <h3 className={css.slideTitle}>{title}</h3>
      </div>
    </div>
  );
};

export default SlideHumanitarianAid;
