import css from './Hero.module.css';
import heroImg from '../../images/Hero.jpg';
import { useSelector } from 'react-redux';

const Hero = () => {
  
  const language = useSelector((state) => state.language);

  const title =
    language === 'de'
      ? 'GEMEINNTZIGER VEREIN ZIELE DER ZUKUNFT E.V.'
      : language === 'en'
      ? 'NON-PROFIT ORGANIZATION GOALS OF THE FUTURE'
      : '   ';
  const content =
    language === 'de'
      ? 'wurde im Jahr 2022 von Brgern aus der Ukraine und Deutschland gegrndet, nachdem Russland eine umfangreiche militrische Intervention in der Ukraine unternommen hatte. Unser Team hat sich zum Ziel gesetzt, humanitre Untersttzung in der Ukraine bereitzustellen und die Integration ukrainischer Brgerinnen und Brger in Erfurt und Deutschland zu frdern.'
      : language === 'en'
      ? 'was founded in 2022 by citizens from Ukraine and Germany after Russia undertook extensive military intervention in Ukraine. Our team aims to provide humanitarian aid in Ukraine and promote the integration of Ukrainian citizens in Erfurt and Germany.'
      : '   2022        ,        .                   .';

  return (
    <div className={css.heroWrap}>
      <div className={css.heroTextWrap}>
        <h1 className={css.heroTitle}>{title}</h1>
        <p className={css.heroText}>{content}</p>
      </div>
      <div className={css.heroImgWrap}>
        <img src={heroImg} alt="Ukrainians with a flag" width="100%" />
      </div>
    </div>
  );
};

export default Hero;




